html {
    box-sizing: border-box;
    font-size: 16px;
    background-color: var(--main-color);
    color: var(--text-color);
    font-family: 'Montserrat', sans-serif;
  }
  
  *, *:before, *:after {
    box-sizing: inherit;
  }
  
  body, h1, h2, h3, h4, h5, h6, p, ol, ul {
    margin: 0;
    padding: 0;
    font-weight: normal;
  }
  
  ol, ul {
    list-style: none;
  }
  
  img {
    max-width: 100%;
    height: auto;
  }

:root {
  --main-color: rgba(91, 209, 215, 0.1);
  --text-color: #767676;;
  --title-color: #181818;
  --button-color: #4fc4cf;
  --button-text: #181818;
}

.container {
  display: flex;
}

@media screen and (max-width: 912px) {
  .container {
    flex-direction: column;
  }
}
  